import $ from "../core";

$.prototype.html = function (text) {
	for (let i = 0; i < this.length; i++) {
		
		if (text) {
			this[i].innerHTML = text;
		} else {
			return this[i].innerHTML;
		}
	}

	return this;
};

$.prototype.eq = function (i) {
	const swap = this[i];
	const objLength = Object.keys(this).length;

	for (let i = 0; i < objLength; i++) {
		delete this[i];
	}

	this[0] = swap;
	this.length = 1;

	return this;
};
